import { NextResponse } from "next/server";
import { rapidFetch } from "../../_utils/rapidapi";

export async function GET(req: Request) {
  const { searchParams } = new URL(req.url);

  const hotel_id = searchParams.get("hotel_id");
  const checkin_date = searchParams.get("checkin_date");
  const checkout_date = searchParams.get("checkout_date");

  if (!hotel_id || !checkin_date || !checkout_date) {
    return NextResponse.json(
      { error: "Missing hotel_id, checkin_date or checkout_date" },
      { status: 400 }
    );
  }

  try {
    const data = await rapidFetch("/v1/hotels/room-list", {
      hotel_id,
      checkin_date,
      checkout_date,
      locale: "en-gb",
      units: "metric",
      currency: "AED",
      adults_number_by_rooms: "3,1",
      children_number_by_rooms: "2,1",
      children_ages: "5,0,9",
    });

    return NextResponse.json(data);
  } catch (err: any) {
    return NextResponse.json({ error: err.message }, { status: 500 });
  }
}
